import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { useSelector } from 'react-redux';
import Layout from '../Layout/Layout';
import {
  adminRoutes,
  subAdminRoutes,
  chefRoutes,
  chefRoutesUnverified,
  promoterRoutes,
  vehicleOwnerRoutes,
} from '../../routes';

const MainContainer = () => {
  const role = useSelector(state => state.auth.userRole);
  const isSubAdmin = useSelector(state => state.auth.isSubAdmin);
  const isPromoter = useSelector(state => state.auth.isPromoter);
  const isvehicleOwner = useSelector(state => state.auth.isvehicleOwner);
  const isApproved = useSelector(state => state.chef.isApproved);

  const routes = (() => {
    if (role === 'chef' && isApproved === null) {
      return [];
    }
    if (role === 'admin') {
      if (isSubAdmin) {
        return subAdminRoutes;
        /*error Unexpected 'else' after 'return'.*/
      } else if (isPromoter) {
        return promoterRoutes;
      } else if (isvehicleOwner) {
        return vehicleOwnerRoutes;
      }
      return adminRoutes;
    }
    if (isApproved === true) {
      return chefRoutes;
    }
    return chefRoutesUnverified;
  })();

  return (
    <Layout>
      <Switch>
        {/* <Route
          exact
          path="/dashboard"
          component={React.lazy(() => import('../Admin/Dashboard/Dashboard'))}
        />
        <Route
          path="/users"
          component={React.lazy(() => import('../Admin/Users/Users'))}
        /> */}
        {routes.map(route =>
          route.component ? (
            <Route
              key={route.name}
              path={route.path}
              exact={route.exact}
              name={route.name}
            >
              <route.component />
            </Route>
          ) : (
            route.redirectRoute && <Redirect key={route.name} to={route.path} />
          ),
        )}
      </Switch>
    </Layout>
  );
};

export default MainContainer;
