import React from 'react';
import { NavLink } from 'react-router-dom';

const ChefSidebar = () => {
  return (
    <>
      <li>
        <NavLink to="/vendors/dashboard" title="Dashboard">
          <i className="fa fa-th" aria-hidden="true" />
          <span className="nav-label">Dashboard</span>
        </NavLink>
      </li>
      <li>
        <NavLink
          to="/vendors/upcoming-orders"
          title="New Orders"
          className="d-flex-center"
        >
          <i className="fa fa-shopping-cart" aria-hidden="true" />
          <span className="nav-label">New Orders</span>
        </NavLink>
      </li>
      <li>
        <NavLink to="/vendors/orders" title="Orders" className="d-flex-center">
          <i className="fa fa-shopping-cart" aria-hidden="true" />
          <span className="nav-label">Orders</span>
        </NavLink>
      </li>
      {/* <li>
        <NavLink to="/vendors/menu-categories" title="Menu Category">
          <i className="fa fa-bars" aria-hidden="true" />
          <span className="nav-label">Menu Category</span>
        </NavLink>
      </li> */}
      <li>
        <NavLink to="/vendors/menu-items" title="Menu items">
          <i className="fa fa-cube" aria-hidden="true" />
          <span className="nav-label">Menu Items</span>
        </NavLink>
      </li>
      <li>
        <NavLink to="/vendors/reviews" title="Review">
          <i className="fa fa-star" aria-hidden="true" />
          <span className="nav-label">Review</span>
        </NavLink>
      </li>
      <li>
        <NavLink to="/vendors/payment-history" title="Payment History">
          <i className="fa fa-money" aria-hidden="true" />
          <span className="nav-label">Payout History</span>
        </NavLink>
      </li>
      <li>
        <NavLink to="/vendors/support-request" title="Support Request">
          <i className="fa fa-support" aria-hidden="true" />
          <span className="nav-label">Support Request</span>
        </NavLink>
      </li>
    </>
  );
};

export default ChefSidebar;
