/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import moment from 'moment';
import {
  editAcceptingOrderChefSaga,
  editMarkAsReadSaga,
  getAcceptingOrderSaga,
  getAllNotificationSaga,
  logout,
} from '../../store/actions';
import { axios } from '../../http';
import SetTokenHeader from '../../hoc/SetTokenHeader/SetTokenHeader';
import AdminSidebar from '../../components/UI/Sidebar/AdminSidebar';
import SubAdminSidebar from '../../components/UI/Sidebar/SubAdminSidebar';
import ChefSidebar from '../../components/UI/Sidebar/ChefSidebar';
import PromoterSidebar from '../../components/UI/Sidebar/PromoterSidebar';
import VehicleOwnerSidebar from '../../components/UI/Sidebar/VehicleOwnerSidebar';
import '../../assets/js/inspinia';
import AlertMessageModal from '../../components/UI/Model/AlertMessageModal';
import DeleteModel from '../../components/UI/Model/DeleteModel';

const Layout = props => {
  const { children } = props;
  // eslint-disable-next-line no-unused-vars
  const [mounted, setMounted] = useState(false);
  const [modelDeleteOpen, setModelDeleteOpen] = useState(false);
  const { acceptOrders, isApproved } = useSelector(state => state.chef);
  const {
    firstName,
    lastName,
    profilePic,
    userRole,
    isSubAdmin,
    isPromoter,
    isvehicleOwner,
  } = useSelector(state => state.auth);
  const {
    notification: { list: allData, totalRecords },
  } = useSelector(state => state.notification);
  const { open } = useSelector(state => state.modal);

  const dispatch = useDispatch();

  useEffect(() => {
    setMounted(true);
    if (userRole === 'chef') {
      dispatch(getAcceptingOrderSaga());
      dispatch(
        getAllNotificationSaga({
          columnName: 'name',
          orderBy: 'asc',
          search: '',
          skip: 0,
          limit: 10,
        }),
      );
    }
    return () => setMounted(false);
  }, []);

  if (children === undefined || children === null || children === '') {
    throw new Error('children is required params');
  }
  const confirmDeleteAction = () => {
    setModelDeleteOpen(false);
    if (userRole === 'chef') {
      dispatch(editAcceptingOrderChefSaga({ status: false, isLogout: true }));
    } else {
      dispatch(logout());
    }
  };
  return (
    <div id="wrapper">
      {(isApproved === true || userRole === 'admin') && (
        <nav
          className="navbar-default navbar-expand-lg 
          navbar-static-side left-main-menu  scrollbar-inner"
          role="navigation"
        >
          <div className="sidebar-collapse">
            <ul className="nav metismenu" id="side-menu">
              <li className="nav-header">
                <div className="dropdown profile-element">
                  <div className="logo-panel text-center float-n">
                    <Link to="/vendors/dashboard">
                      <img
                        src={require('../../assets/img/logo-modal.png')}
                        alt=""
                      />
                    </Link>
                  </div>
                </div>
                <div className="logo-element">
                  <Link to="/vendors/dashboard">
                    <img
                      src={require('../../assets/img/logo-icon.png')}
                      alt=""
                    />
                  </Link>
                </div>
              </li>
              {
                // eslint-disable-next-line no-nested-ternary
                userRole === 'admin' ? (
                  // eslint-disable-next-line no-nested-ternary
                  isSubAdmin ? (
                    <SubAdminSidebar />
                  ) : // eslint-disable-next-line no-nested-ternary
                  isPromoter ? (
                    <PromoterSidebar />
                  ) : // eslint-disable-next-line no-nested-ternary
                  isvehicleOwner ? (
                    <VehicleOwnerSidebar />
                  ) : (
                    <AdminSidebar />
                  )
                ) : (
                  <ChefSidebar />
                )
              }
            </ul>
          </div>
        </nav>
      )}
      <div
        id="page-wrapper"
        className="dark-bg"
        style={
          isApproved === true || userRole === 'admin'
            ? { minHeight: '615px' }
            : { minHeight: '615px', margin: 0 }
        }
      >
        <div className="row top-menu2">
          <nav
            className="navbar navbar-static-top dark-bg-header top-menu d-flex-a"
            role="navigation"
            style={
              isApproved === true && userRole === 'chef'
                ? { marginBottom: '0' }
                : { marginBottom: '0', height: '70px' }
            }
          >
            <div
              className="navbar-header d-flex-center col-lg-6 col-md-6 col-sm-6 col-xs-6"
              style={
                isApproved === true && userRole === 'chef' ? {} : { top: 0 }
              }
            >
              {isApproved === true || userRole === 'admin' ? (
                <a className="navbar-minimalize minimalize-styl-2 btn btn-primary">
                  <i className="fa fa-bars" />
                </a>
              ) : (
                <>
                  <div className="logo-panel text-center float-n header-logo-img">
                    <img
                      src={require('../../assets/img/logo-modal.png')}
                      alt=""
                    />
                  </div>
                  <div className="logo-element header-logo-img-sm">
                    <img
                      src={require('../../assets/img/logo-icon.png')}
                      alt=""
                    />
                  </div>
                </>
              )}
            </div>
            <div
              className=" width-150a col-lg-4"
              style={{ position: 'absolute' }}
            >
              {userRole === 'chef' &&
                acceptOrders !== null &&
                isApproved === true && (
                  <>
                    <label className="switch">
                      <input
                        type="checkbox"
                        checked={acceptOrders}
                        onChange={() => {
                          dispatch(
                            editAcceptingOrderChefSaga({
                              status: !acceptOrders,
                            }),
                          );
                        }}
                      />
                      <span className="slider round" />
                    </label>
                    <span className="m-l-xs">Start Accepting Orders </span>
                    {open && <AlertMessageModal />}
                  </>
                )}
            </div>
            <div
              className="col-lg-6 col-lg-6 col-md-6 col-sm-6 
            col-xs-12 nav navbar-top-links navbar-right"
              style={
                isApproved === true && userRole === 'chef' ? {} : { top: 0 }
              }
            >
              <ul className="nav navbar-top-links navbar-right">
                {userRole === 'chef' && isApproved && (
                  <li className="dropdown m-r-sm">
                    <a
                      className="dropdown-toggle count-info"
                      data-toggle="dropdown"
                    >
                      <i className="fa fa-bell" />
                      {totalRecords.unreadCount > 0 && (
                        <span className="label label-primary">
                          {/* eslint-disable-next-line no-nested-ternary */}
                          {/* {totalRecords.unreadCount
                            ? totalRecords.unreadCount <= 99
                              ? totalRecords.unreadCount
                              : '99+'
                            : 0} */}
                        </span>
                      )}
                    </a>
                    <ul
                      className="dropdown-menu dropdown-alerts"
                      id="notification-list"
                      style={{
                        maxHeight: '300px',
                        overflow: 'auto',
                      }}
                    >
                      {allData.length === 0 && (
                        <div
                          style={{ textAlign: 'center', margin: '80px 0px' }}
                        >
                          No notification avaliable
                        </div>
                      )}
                      {allData.slice(0, 9).map(item => (
                        <li key={item._id}>
                          {item.redirectURL !== '' ? (
                            // eslint-disable-next-line jsx-a11y/anchor-is-valid
                            <Link
                              to={item.redirectURL}
                              onClick={() =>
                                dispatch(editMarkAsReadSaga({ id: item._id }))
                              }
                            >
                              <div>
                                <i className="fa fa-envelope fa-fw" />
                                {item.message}
                                <div
                                  style={{ width: '100%', textAlign: 'right' }}
                                >
                                  <span className="text-muted small">
                                    {moment(item.createdAt).format(
                                      'DD/MM/YYYY',
                                    )}
                                  </span>
                                </div>
                              </div>
                            </Link>
                          ) : (
                            <a
                              onClick={() =>
                                dispatch(editMarkAsReadSaga({ id: item._id }))
                              }
                            >
                              <div>
                                <i className="fa fa-envelope fa-fw" />
                                {item.message}
                                <div
                                  style={{ width: '100%', textAlign: 'right' }}
                                >
                                  <span className="text-muted small">
                                    {moment(item.createdAt).format(
                                      'DD/MM/YYYY',
                                    )}
                                  </span>
                                </div>
                              </div>
                            </a>
                          )}
                        </li>
                      ))}
                    </ul>
                  </li>
                )}
                <li className="user-info-box welcome tooltipa">
                  <div className="dropdown d-flex">
                    {userRole === 'admin' && (
                      <span className="prof-img">
                        <Link to="/vendors/profile">
                          {profilePic ? (
                            <img
                              alt="userimage"
                              className="img-back"
                              key={profilePic}
                              src={`${profilePic}`}
                            />
                          ) : (
                            <img
                              alt="userimage"
                              className="img-back"
                              src={require('../../assets/img/user-image.png')}
                            />
                          )}
                        </Link>
                      </span>
                    )}
                    <Link
                      data-toggle="dropdown"
                      className="dropdown-toggle"
                      to="/vendors/"
                      aria-expanded="false"
                    >
                      <span className="clear">
                        <span className="block m-t-xs" />
                        <span className="text-muted text-xs block">
                          {firstName.trim() !== '' || lastName.trim() !== ''
                            ? `${firstName} ${lastName}`
                            : '...'}
                          <b className="caret" />
                        </span>
                      </span>
                    </Link>
                    <ul className="dropdown-menu">
                      <li>
                        <Link to="/vendors/profile">Profile</Link>
                      </li>
                      <li>
                        <button
                          type="button"
                          className="btn-no-bg color-black"
                          style={{
                            marginLeft: '5px',
                            fontWeight: 'normal',
                            borderRadius: 0,
                          }}
                          // to="/vendors/home-login"
                          onClick={() => setModelDeleteOpen(true)}
                        >
                          Logout
                        </button>
                      </li>
                    </ul>
                  </div>
                </li>
              </ul>
            </div>
          </nav>
        </div>
        {children}
      </div>
      {modelDeleteOpen && (
        <DeleteModel
          setModelOpen={setModelDeleteOpen}
          submitBtnText="Yes"
          submitBtnHandler={confirmDeleteAction}
          cancelBtnText="No"
          closeModel={setModelDeleteOpen}
          from="Logout"
        />
      )}
    </div>
  );
};

export default SetTokenHeader(Layout, axios);
